.columns-game .menu-wrapper {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

.columns-game .menu-item {
    color: #2c2c2c;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 5px;
}

.columns-game .menu-item > a {
    color: #2C2C2C;
    text-decoration: none;
}

.columns-game .menu-item > span {
    display: inline-block;
    padding: 10px;
    line-height: 10px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.columns-game .menu-item > span:hover {
    background: dodgerblue;
}


