.menu-wrapper {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

.menu-item {
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 5px;
}

.menu-item > a {
    color: #fff;
    text-decoration: none;
}

.menu-item > span {
    display: inline-block;
    padding: 10px;
    line-height: 10px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.menu-item > span:hover {
    background: red;
}


