

.soroban-container{
    display:-ms-flexbox;
    display:flex;
    position:absolute;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    top: 50%;
    width: 100%;
    min-height: 30vh;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-flow: row;
        flex-flow: row;
    /*padding: 5px;*/
    z-index: 0;
}

.soroban-content-block{
    display: -ms-flexbox;
    display: flex;
    width: 80px;
    height: 240px;
    z-index: 1;
    border-top: 1px solid grey;
    border-right: 1px solid grey;
    border-bottom: 1px solid grey;
    position: relative;
}

.soroban-rail {
    height: 100%;
    width: calc(50% - 3px);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 200;
    border-width: 0 6px 0 0;
    border-style: solid;
    -webkit-border-image:
            -webkit-gradient(linear, 100% 0, 0 0, from(#484848), to(#a9a9a9)) 1 100%;
    -webkit-border-image:
            -webkit-linear-gradient(right, #484848, #a9a9a9) 1 100%;
    -o-border-image:
            -o-linear-gradient(right, #484848, #a9a9a9) 1 100%;
    border-image:
            -webkit-gradient(linear, left top, right top, from(#484848), to(#a9a9a9)) 1 100%;
    border-image:
            linear-gradient(to right, #484848, #a9a9a9) 1 100%;
}

.soroban-content-block:first-of-type{
    border-left: 1px solid grey;
}

.soroban-jo{
    position: absolute;
    z-index: 201;
    width: 100%;
    height: 100%;
    -ms-flex:1 1;
        flex:1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 2px 0;
}
.soroban-jo1{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    border-bottom: 4px solid #cdcdcd;
    padding: 0 2px 3px 2px;
    margin-bottom: 4px;
}
.soroban-jo2{
    -ms-flex: 3 1;
        flex: 3 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0 2px 2px 2px;
}

.soroban-bone{
    -ms-flex:1 1;
        flex:1 1;
    /*border: 1px solid lime;*/
    margin-bottom:2px;
    text-align: center;
    font-weight: bold;
    font-size: 1.3rem;
    z-index: 1;
    height: 20px;
}

.soroban-zero{
    border:none;
}

.soroban-one{
    border-radius: 60%;
    background: -webkit-gradient(linear, left top, left bottom, from(#ff6d00), color-stop(45%, #bb4015), color-stop(60%, #634945), to(#2a1615));
    background: -webkit-linear-gradient(#ff6d00 0%, #bb4015 45%, #634945 60%, #2a1615);
    background: -o-linear-gradient(#ff6d00 0%, #bb4015 45%, #634945 60%, #2a1615);
    background: linear-gradient(#ff6d00 0%, #bb4015 45%, #634945 60%, #2a1615);
}

.soroban-cont{
    padding:10px;
}


