.group-list-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 50px;
    line-height: 50px;
    border-bottom: 2px solid #dee2e6;
    border-top: 1px solid #dee2e6;
    /*background: #0d47a1;*/
    /*color:white;*/
}

.group-list-header-item {
    font-weight: 700;
    font-size: 0.95rem;
    padding: 0 10px;
}

.group-list-header-item:nth-of-type(1){
    -ms-flex: 3 1;
        flex: 3 1;
}
.group-list-header-item:nth-of-type(2){
    -ms-flex: 1 1;
        flex: 1 1;
}
.group-list-header-item:nth-of-type(3), .group-list-header-item:nth-of-type(4){
    -ms-flex: 2 1;
        flex: 2 1;
}



