.trainee-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid #dee2e6;
    height: 50px;
    line-height: 50px;
    -webkit-transition: .24s;
    -o-transition: .24s;
    transition: .24s;
    font-size: 0.95rem;
}

.trainee-row:hover{
    background: rgba(0,0,0,.075);
}

.trainee-item {
    padding: 0 10px;
}

.trainee-item:nth-of-type(1){
    -ms-flex: 1 1;
        flex: 1 1;
}
.trainee-item:nth-of-type(2){
    -ms-flex: 3 1;
        flex: 3 1;
}
.trainee-item:nth-of-type(3), .trainee-item:nth-of-type(4){
    -ms-flex: 2 1;
        flex: 2 1;
}

.trainee-item img {
    margin-top: 20px;
    height: 40px;
    border-radius: 50%;
}

.trainee-item-well {
    padding: 10px;
    border-radius: 3px;
    background: rgb(251, 244, 255);
    border: 1px solid #dee2e6;
    text-decoration: none;
    font-size: 0.8rem;
    color: #646566;
    -webkit-transition: 0.24s;
    -o-transition: 0.24s;
    transition: 0.24s;
    cursor: pointer;
}

.trainee-item-well:hover, .trainee-item-well:active {
    border: 1px solid #b6babe;
}

.trainee-item a {
    text-decoration: none;
}


