.login-error {
    display: -ms-flexbox;
    display: flex;
    background: #ff6b70;
    border-radius: 3px;
    color: #fff;
    padding: 10px;
    font-size: .9rem;
    margin-bottom: 20px;
    line-height: 20px;
    max-width: 400px;
}


.login-error-controls {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.login-error-hide {
    padding: 10px 5px;
    cursor: pointer;
}

.login-error-hide span::before, .login-error-hide span::after {
    content: '';
    display: block;
    height: 2px;
    width: 10px;
    background: #fff;
}

.login-error-hide span::before {
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.login-error-hide span::after {
    margin-top: -2px;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.login-error-label {
    -ms-flex: 20 1;
        flex: 20 1;
    padding-right: 10px;
}


