.side-widget-panel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: end;
        align-items: flex-end;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.side-widget-panel a {
    width: 100px;
    height: 100px;
    margin: 10px 10px 0 0;
    border: 1px solid #1161ff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border-radius: 3px;
    background: #0d47a1;
    color: rgba(255,255,255,0.75);
    text-decoration: none;
    cursor: pointer;
}

.side-widget-panel a:hover {
    background: #0e52d8;
    color: rgba(255,255,255,1);
}


