.soroban-ai-wrapper {
    position: absolute;
    top: 70%;
    left: 0;
    width: 100%;
    height: 10%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
