.group-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid #dee2e6;
    height: 50px;
    line-height: 50px;
    -webkit-transition: .24s;
    -o-transition: .24s;
    transition: .24s;
    font-size: 0.95rem;
}

.group-item:hover{
    background: rgba(0,0,0,.075);
}

.group-item-placeholder {
    padding: 0 10px;
}

.group-item-placeholder:nth-of-type(1){
    -ms-flex: 3 1;
        flex: 3 1;
}
.group-item-placeholder:nth-of-type(2){
    -ms-flex: 1 1;
        flex: 1 1;
}
.group-item-placeholder:nth-of-type(3), .group-item-placeholder:nth-of-type(4){
    -ms-flex: 2 1;
        flex: 2 1;
}



