.navbar-wrapper {
    width: 100%;
    background-color: #0d47a1;
    color: white !important;
    padding-bottom: 5px;
}

.navbar {
    width: 100%;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    /*justify-content: space-between;*/
}

.nav-logo {
    -ms-flex:1 0 140px;
        flex:1 0 140px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.nav-logo a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    text-decoration: none;
}

.nav-logo img {
    display: -ms-inline-flexbox;
    display: inline-flex;
    height: 40px;
}

.nav-logo span {
    display: -ms-inline-flexbox;
    display: inline-flex;
    text-transform: uppercase;
    font-size: 0.9rem;
    font-weight: 700;
    margin-left: 7px;
    color: white;
}

.nav-left {
    -ms-flex: 9 0 500px;
        flex: 9 0 500px;
    min-width: 900px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.nav-right {
    -ms-flex:2 0;
        flex:2 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.nav-offset-1 {
    -ms-flex: 1 0;
        flex: 1 0;
}



